<template>
	<div class="detail">
		<img class="image" mode="widthFix" :src="dataSource.row.pic || dataSource.row.coverpic">
		<div>{{dataSource.row.intro}}</div>
		<list :dataList="dataSource.vodrows"></list>
	</div>
</template>

<script>
import config from "../js/config.js";
import list from "../components/list.vue";
import { mapState, mapMutations, mapGetters, mapActions } from "vuex";
export default {
  components: {
    list
  },
  data() {
    return {
      dataSource: {
        row: {}
      },
      spid: ""
    };
  },
  computed: {
    ...mapState({})
  },
  created() {
    this.spid = this.$route.query.spid;
    this.loadData();
  },
  methods: {
    loadData() {
      console.log("config", config, config.query());
      this.$axios
        .request({
          url: config.origin + "/special/detail/" + this.spid,
          params: config.query()
        })
        .then(res => {
          console.log("success", res);
          if (res.status == 200 && res.data.retcode == 0) {
            this.dataSource = res.data.data;
          } else {
			weui.topTips(res.data.errmsg || res.statusCode || "fail");
          }
        })
        .catch(res => {
          weui.topTips(res.message || "fail");
        });
    }
  }
};
</script>

<style lang="less" scoped>
.detail {
  .image {
    width: 100vw;
    margin-bottom: 5px;
  }
}
</style>
